@use '../../styles/_mixins.scss';

.navigationBottom {
  position: fixed;
  bottom: 0;
  width: 100%;
  border-top: 2px solid #461ca954;
  background-color: #eee;
  box-shadow: 0 1px 0 #fff;
}

.containerBottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 20px;
  max-width: 1280px;
  margin: 0 auto;
}

.circleBox {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  box-shadow: 0 0 50px 5px rgba(0, 0, 0, 0.3);
  background-color: #fff;
  @include mixins.mq(tablet) {
    width: 60px;
    height: 60px;
  }
}
